.banner {
  background:transparent

}
.centretext{
  text-align: center;
  padding: 20px 0;
  max-width: 600px;
  margin-top: 20px;
  margin-bottom: 20px;
}

.solidline {
  border-bottom: 3px solid rgb(80, 195, 230);
  min-width: 300px;
  max-width: 350px;
  min-height: 30px;
}
.blankspace{
  min-height: 20px;
  background:transparent;
}

.banner-small {
  background:transparent
}

@keyframes flicker {
  0% {
    opacity: 0.6;
  }
  2% {
    opacity: 1;
  }
  4% {
    opacity: 0.5
  }

  8% {
    opacity: 1;
  }
  70% {
    opacity: 0.7;
  }
  100% {
    opacity: 1;
  }
}
